        <h2>Что такое калькулятор любви</h2>
        <p>Это маленький онлайн-инструмент для развлечения: введите два имени и получите процент совместимости.</p>
        <p>Он не претендует на серьёзную психологию и создан для лёгких моментов — мемы, тосты, вечеринки, чат с друзьями.</p>

        <h3>Как работает</h3>
        <p>Мы соединяем два имени, считаем сумму кодов символов и преобразуем её в процент от 1 до 100. Одинаковые имена всегда дают одинаковый результат.</p>

