<script setup>
import { ref } from 'vue'
import { useHead } from '#imports'

const name1 = ref('')
const name2 = ref('')
const score = ref(null)
const message = ref('')
const hasResult = ref(false)

function calculate() {
  const a = (name1.value || '').trim().toLowerCase()
  const b = (name2.value || '').trim().toLowerCase()
  if (!a || !b) {
    alert('Введите оба имени')
    return
  }

  const combined = a + b
  let total = 0
  for (let i = 0; i < combined.length; i += 1) {
    total += combined.charCodeAt(i)
  }
  const seed = combined.length * 13 + total
  const percent = (seed % 100) + 1
  score.value = percent
  message.value = getMessage(percent)
  hasResult.value = true
}

function getMessage(pct) {
  if (pct >= 90) return 'Идеальная пара'
  if (pct >= 70) return 'Отличный результат, у вас большое будущее'
  if (pct >= 50) return 'Неплохо, но придётся поработать над отношениями'
  if (pct >= 30) return 'Интересная комбинация, возможно это испытание судьбы'
  return 'Пока не идеально, но всё можно изменить'
}

useHead({
  title: 'Калькулятор любви | Neural Wise Wolf',
  meta: [
    {
      name: 'description',
      content: 'Забавный онлайн калькулятор любви. Введите два имени и узнайте процент совместимости.'
    }
  ]
})
</script>

